/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

body {
  min-width: 320px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Ubuntu-Regular", Arial, sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  color: #332f2f;
}

body.is-locked {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 0;
  overflow: hidden;
}

.container, .nav__body {
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 15px;
}

.row {
  display: flex;
  margin: 0 -15px;
}

.main {
  overflow: hidden;
  padding-top: 130px;
}

.section {
  position: relative;
}

.section-title {
  position: relative;
}

.section-title::before {
  position: absolute;
  width: 3px;
  content: '';
}

.section-title_left {
  padding-left: 30px;
  text-align: left;
}

.section-title_left::before {
  top: 0;
  left: 0;
  height: 100%;
  background-color: #cf000f;
  content: '';
}

.section-title_center {
  text-align: center;
}

.section-title_center::after {
  left: calc(50% - 2px);
  bottom: 0;
  height: 25px;
  content: '';
}

.section-title__inner {
  display: flex;
}

.section-title_center .section-title__inner {
  justify-content: center;
}

.section-title_left .section-title__inner {
  justify-content: flex-start;
}

.section-title__icon {
  margin: -5px -20px 0 0;
  flex-shrink: 0;
}

.section-title__text {
  font-family: "Ubuntu-Bold", Arial, sans-serif;
  font-weight: normal;
  font-size: 40px;
  line-height: 60px;
  text-transform: uppercase;
}

.section-title_black .section-title__text {
  color: #1a1818;
}

.section-title_white .section-title__text {
  color: #fff;
}

.section-title__text span {
  font-family: "Ubuntu-Light", Arial, sans-serif;
  font-weight: normal;
}

.section-title__descr {
  margin-top: 10px;
  color: #332f2f;
  font-family: "Ubuntu-Light", Arial, sans-serif;
  font-weight: normal;
  font-size: 22px;
  line-height: 34px;
}

.section-title__descr_color-1 {
  color: #e6e6e6;
}

.section-title__descr_color-2 {
  color: #e5e5e5;
}

.title {
  font-family: "Ubuntu-Bold", Arial, sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  text-transform: uppercase;
}

.title_black {
  color: #1a1818;
}

.title_white {
  color: #fff;
}

.btn {
  position: relative;
  display: inline-block;
  padding: 16px 42px;
  outline: 0;
  font-family: "Ubuntu-Medium", Arial, sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  text-align: center;
  border-radius: 28px;
  cursor: pointer;
}

.btn::after, .btn::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 28px;
  content: '';
}

.btn span {
  position: relative;
  z-index: 1;
}

.btn_red {
  color: #fff;
}

.btn_red:hover::after {
  transform: translateY(0);
  box-shadow: none;
  content: '';
}

.btn_red::after {
  transform: translateY(5px);
  background-color: #9c1019;
  box-shadow: 0 0 48px 3px rgba(240, 67, 79, 0.2);
  transition: .3s ease-in-out;
}

.btn_red::before {
  z-index: 1;
  background-color: #cf000f;
}

.arc-decor {
  position: absolute;
  width: 100%;
  z-index: 2;
  bottom: 0;
  height: 150px;
  overflow: hidden;
}

.arc-decor__mask {
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
