.nav {
  height: 50px;
  box-shadow: 0 15px 35px rgba(26, 23, 23, 0.05);
  background-color: #5e0f17;
}

@media only screen and (max-width: 1199px) {
  .nav {
    flex-direction: column;
    justify-content: center;
    position: absolute;
    top: 100px;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 10;
    padding-bottom: 100px;
    overflow: auto;
    opacity: 0;
    border: 0;
    visibility: hidden;
    transform: translate3d(0, 100px, 0);
    transition: transform .4s ease, opacity .4s ease, visibility 0s .4s, -webkit-transform .4s ease;
    background: #fff;
    text-align: center;
    box-shadow: inset 0 0 25px rgba(28, 155, 238, 0.5);
  }
}

@media only screen and (max-width: 767px) {
  .nav {
    top: 70px;
    padding-bottom: 70px;
  }
}

.nav.is-active {
  opacity: 1;
  visibility: visible;
  transform: translate3d(0, 0, 0);
  transition: transform .4s ease, opacity .4s ease, visibility 0s 0s, -webkit-transform .4s ease;
}

.nav__body {
  height: 100%;
}

.nav__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

@media only screen and (max-width: 1199px) {
  .nav__list {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: auto;
    height: 100%;
    padding-bottom: 0;
  }
}

.nav__list-item {
  position: relative;
}

@media only screen and (max-width: 1199px) {
  .nav__list-item {
    opacity: 0;
    transform: translateY(20px);
    transition: .3s ease-in-out;
  }
}

.is-active .nav__list-item {
  opacity: 1;
  transform: translateY(0);
}

.nav__list-item:not(:last-child)::after {
  position: absolute;
  top: 50%;
  right: -50%;
  width: 4px;
  height: 4px;
  margin-top: -2px;
  margin-right: -2px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.2);
  content: '';
}

@media only screen and (max-width: 1199px) {
  .nav__list-item:not(:last-child)::after {
    display: none;
  }
}

.nav__list-item:nth-child(1) {
  transition-delay: 0s;
}

.nav__list-item:nth-child(2) {
  transition-delay: 0.1s;
}

.nav__list-item:nth-child(3) {
  transition-delay: 0.2s;
}

.nav__list-item:nth-child(4) {
  transition-delay: 0.3s;
}

.nav__list-item:nth-child(5) {
  transition-delay: 0.4s;
}

.nav__list-link {
  display: block;
  font-size: 16px;
  line-height: 16px;
  color: #fff;
  transition: .3s ease-in-out;
}

@media only screen and (max-width: 1199px) {
  .nav__list-link {
    color: #cf000f;
    font-size: 16px;
  }
}

.nav__list-link:hover {
  color: #cf000f;
}

.nav__btn {
  display: none;
  position: relative;
  top: -5px;
  border: 0;
  outline: 0;
  padding: 0;
  z-index: 11;
}

@media only screen and (max-width: 1199px) {
  .nav__btn {
    display: block;
  }
}

.nav__btn.is-active span {
  color: #cf000f;
}

.nav__btn-burger {
  display: block;
  margin: auto;
  width: 40px;
  height: 40px;
  cursor: pointer;
}

.nav__btn-lines {
  display: block;
  width: 40px;
  height: 3px;
  position: relative;
  transform: translateY(25px);
  background-image: linear-gradient(to right, #1799ed 0%, #59bffa 100%);
  transition: all .3s;
}

.nav__btn-lines:after, .nav__btn-lines:before {
  width: 40px;
  height: 3px;
}

.nav__btn.is-active .nav__btn-lines {
  background: rgba(255, 255, 255, 0);
}

.nav__btn-lines:before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 10px;
  background-image: linear-gradient(to right, #1799ed 0%, #59bffa 100%);
  transition: bottom 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
}

.nav__btn-lines:after {
  content: "";
  position: absolute;
  left: 0;
  top: 10px;
  background-image: linear-gradient(to right, #1799ed 0%, #59bffa 100%);
  transition: top 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
}

.nav__btn.is-active .nav__btn-lines:after {
  top: 0;
  transform: rotate(45deg);
  transition: top 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1);
}

.nav__btn.is-active .nav__btn-lines:before {
  bottom: 0;
  transform: rotate(-45deg);
  transition: bottom 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1);
}

.form-field {
