  font-size: 15px;
  line-height: 46px;
  border-radius: 30px;
  background-color: #f9f9f9;
  border: 1px solid #e6e6e6;
  transition: .3s ease-in-out;
}

.form-field__input:-moz-placeholder, .form-field__input::-moz-placeholder {
  color: #75797b;
