}

.form-field.has-error .form-field__input {
  border-color: #d51d48;
}

.form-field__icon {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 60px;
  color: #3198f0;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
}

@media only screen and (max-width: 767px) {
  .form-field__icon {
    width: 40px;
  }
}

.form-field.has-error .form-field__icon {
  color: #d51d48;
}

.form-field__icon::after {
  position: absolute;
  top: 50%;
  right: 0;
  margin-top: -15px;
  width: 1px;
  height: 30px;
  background-color: #ebebeb;
  content: '';
}

.form-field__icon .zmdi {
  position: relative;
  top: -1px;
  right: -4px;
}

.textarea {
  display: block;
  width: 100%;
  height: 122px;
  padding: 18px 36px;
  resize: none;
  box-shadow: 0 15px 40px rgba(18, 3, 58, 0.15);
  border-radius: 23px;
  background-color: #ebe9f2;
  border: 0;
  font-family: Arial, sans-serif;
  font-size: 14px;
  line-height: 20px;
  transition: .3s ease-in-out;
}

.textarea:focus {
  background-color: #fff;
}

.form-submit-btn {
  cursor: pointer;
}

.form-submit-btn input {
  display: none;
}

.form-ask {
  display: flex;
  align-items: center;
  font-size: 15px;
}

.form-ask__text {
  margin-right: 23px;
  color: #333333;
}

.form-ask__choice {
  display: flex;
  align-items: center;
}

.form-ask__choice-item {
  display: flex;
  cursor: pointer;
}

.form-ask__choice-item:not(:last-child) {
  margin-right: 15px;
}

.form-ask__choice-item input {
  display: none;
}

.form-ask__choice-item input:checked + .form-ask__choice-visual {
  border-color: #1799ed;
}

.form-ask__choice-item input:checked + .form-ask__choice-visual::after {
  opacity: 1;
}

.form-ask__choice-text {
  color: #75797b;
}

.form-ask__choice-visual {
  position: relative;
  width: 18px;
  height: 18px;
  margin-right: 8px;
  border: 1px solid rgba(23, 153, 237, 0.5);
  border-radius: 50%;
  transition: .3s ease-in-out;
}

.form-ask__choice-visual::after {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 10px;
  height: 10px;
  background-image: linear-gradient(to right, #1799ed 0%, #59bffa 100%);
  border-radius: 50%;
  transition: .3s ease-in-out;
  opacity: 0;
  content: '';
}

.popup {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  z-index: 1300;
  overflow: auto;
  outline: 0;
  text-align: center;
  font-size: 0;
  transform: translate3d(0, 0, 0);
  background: rgba(0, 0, 0, 0.6);
  transition: .3s ease-in-out;
  will-change: opacity, transform;
}

.popup:after {
  content: '';
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.popup.opened {
  opacity: 1;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
  visibility: visible;
}

.popup__close {
  position: absolute;
  top: -35px;
  right: 30px;
  z-index: 10;
  cursor: pointer;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 0;
}

.popup__close:hover svg {
  fill: #cf000f;
}

.popup__close svg {
  fill: #fff;
  width: 24px;
  height: 24px;
  transition: all 0.25s ease-out;
}

.popup__container {
  display: inline-block;
  vertical-align: middle;
  max-width: 874px;
  width: 100%;
  position: relative;
  padding: 0 15px;
  margin: 30px auto;
  transition: opacity .5s ease, transform .5s ease;
  transform: translate3d(0, -100px, 0);
  will-change: transform, opacity;
  text-align: left;
  border-radius: 5px;
}

.popup__container_xs {
  max-width: 440px;
}

.popup__container_lg {
  max-width: 855px;
}

.popup__inner {
  padding: 45px 25px 50px;
  box-shadow: 0 8px 20px rgba(6, 53, 83, 0.13);
  border-radius: 17px;
  background-color: #ffffff;
}

@media only screen and (max-width: 767px) {
  .popup__inner {
    padding: 30px 25px 35px;
  }
}

.popup__inner p {
  color: #5c6164;
  font-size: 18px;
  line-height: 24px;
  text-align: center;
}

@media only screen and (max-width: 767px) {
  .popup__inner p {
    font-size: 16px;
  }
}

body.popup-opened {
  overflow: hidden;
}

.popup.opened .popup__container {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.thanks-popup {
  text-align: center;
}

.thanks-popup__title {
  margin-bottom: 14px;
  text-transform: uppercase;
}

.thanks-popup__btn {
  display: none;
}

.slider {
  display: none;
}

.slider.slick-initialized {
  display: block;
}

.slider-controls {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 150px;
  height: 20px;
  z-index: 1;
}

.slider-dots {
  display: flex;
  justify-content: center;
  padding: 0 20px;
  line-height: 0;
}

.slider-dots li:not(:last-child) {
  margin-right: 24px;
}

.slider-dots li.slick-active button {
  background-color: #cf000f;
}

.slider-dots li button {
  width: 6px;
  height: 6px;
  background-color: #808080;
  border-radius: 50%;
  font: 0/0 a;
  text-shadow: none;
  color: transparent;
  transition: .3s ease-in-out;
}

.slider-btn {
  position: absolute;
  top: 0;
  transition: .3s ease-in-out;
}

.slider-btn:hover {
  opacity: .5;
}

.slider-btn_prev {
  left: 0;
}

.slider-btn_next {
  right: 0;
}

.slider-btn svg {
  fill: #808080;
  font-size: 22px;
}

.screen__container {
  display: flex;
  align-items: center;
  height: 100%;
}

.screen__slider .slick-track,
.screen__slider .slick-list {
  -webkit-perspective: 2000px !important;
  -webkit-backface-visibility: hidden !important;
}

.screen__slider-item {
  position: relative;
  height: 620px;
}

.screen__slider-controls {
  top: 50px;
  left: 0;
}

.screen__slider-controls-wrap {
  position: relative;
}

.screen__bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

.screen__content {
  flex: 0 1 50%;
  width: 50%;
}

.screen__content-title {
  margin-bottom: 10px;
}

.screen__content-bot {
  display: flex;
  align-items: center;
}

.screen__content-price {
  display: flex;
  align-items: center;
  margin-right: 30px;
}

.screen__content-price-text {
  color: #d9d9d9;
}

.benefits {
  padding: 60px 0;
}

.benefits__title {
  margin-bottom: 30px;
}

.benefits__item {
  position: relative;
  flex: 0 1 calc(25% - 30px);
  width: calc(25% - 30px);
  margin: 0 15px;
  padding: 20px 25px;
  background-color: #fff;
  cursor: default;
}

.benefits__item-decor {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  box-shadow: 0 15px 40px rgba(26, 3, 4, 0.1);
  border: 1px solid #f7f7f7;
  transition: .3s ease-in-out;
  pointer-events: none;
}

.benefits__item:hover .benefits__item-decor {
  opacity: .5;
}

.benefits__item-decor::before, .benefits__item-decor::after {
  position: absolute;
  width: 15px;
  height: 15px;
  content: '';
}

.benefits__item-decor::before {
  top: 0;
  left: 0;
  border-top: 2px solid #cf000f;
  border-left: 2px solid #cf000f;
}

.benefits__item-decor::after {
  right: 0;
  bottom: 0;
  border-right: 2px solid #cf000f;
  border-bottom: 2px solid #cf000f;
}

.benefits__item-top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
}

.benefits__item-title {
  flex: 0 1 calc(100% - 50px);
}
